import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { HomePage } from './containers/Home';
import { ProductListPage } from './containers/ProductList';
import './App.css';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { isUserLoggedIn, updateCart } from './actions';
import { ProductDetailsPage } from './containers/ProductList/ProductDetailsPage';
import { CartPage } from './containers/CartPage';
import { CheckoutPage } from './containers/Checkout';
import { OrderPage } from './containers/Order';
import { OrderDetailsPage } from './containers/OrderDetails';


function App() {

  const dispatch = useDispatch();

  const auth = useSelector(state => state.auth);

  useEffect(() => {
    
    if(!auth.authenticate){
      dispatch(isUserLoggedIn());
    }

  }, [ auth.authenticate ])

  useEffect(() => {
    
    dispatch(updateCart());
  }, [auth.authenticate])


  return (
    <div className="App">
      <Router>
        <Switch>
          <Route path="/" exact component={ HomePage }/>
          <Route path="/cart" component={ CartPage }/>
          <Route path="/checkout" component={ CheckoutPage }/>
          <Route path="/account/orders" component={ OrderPage} />
          <Route path="/order_details/:orderId" component={ OrderDetailsPage} />
          <Route path="/:productSlug/:productId/p" exact component={ ProductDetailsPage }/>
          <Route path="/:slug" exact component={ ProductListPage }/>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
