import { cartConstants } from "../actions/constants";


const initialState = {
    cartItems: {

    },
    updatingCart : false,
    error: null
};


export const cartReducer = ( state = initialState, action ) => {

    switch(action.type){

        case cartConstants.ADD_TO_CART_REQUEST:
            state = {
                ...state,
                updatingCart: true
            }
            break;
        case cartConstants.ADD_TO_CART_SUCCESS:
            state = {
                ...state,
                cartItems: action.payload.cartItems,
                updatingCart: false
            }
            break;
        case cartConstants.ADD_TO_CART_FAILURE:
            state = {
                ...state,
                error: action.payload.error,
                updatingCart: false
            }
            break;

        case cartConstants.RESET_CART: 
            state = {
                ...initialState
            }

        // case cartConstants.ADD_NEW_CATEGORY_REQUEST:
        //     state = {
        //         ...state,
        //         loading: true
        //     }
        //     break;
        
        // case cartConstants.ADD_NEW_CATEGORY_SUCCESS:
            
        //     const category =  action.payload.category;
        //     const updatecat = buildNewCategories( category.parentId ,state.categories, category);

        //     state = {
        //         ...state,
        //         loading: false,
        //         categories: updatecat,
        //     }
        //     break;
        
        // case cartConstants.ADD_NEW_CATEGORY_FAILURE:
        //     state = {
        //         ...initialState
        //     }
        //     break;
    }

    return state;
}